import {
	classNames,
	widgetSettings,
	lerpColor,
	getFuelNeeded
} from './../../lib/utils';
import { action, observable } from 'mobx';
import { IWidgetSetting, lowPerformanceMode, showAllMode } from '../app/app';
import { observer } from 'mobx-react';
import _ from './../../translate';
import r3e, { registerUpdate, unregisterUpdate } from '../../lib/r3e';
import React from 'react';
import style from './fuel.scss';
import SvgIcon from '../svgIcon/svgIcon';

interface IProps extends React.HTMLAttributes<HTMLDivElement> {
	settings: IWidgetSetting;
}

@observer
export default class Fuel extends React.Component<IProps, {}> {
	@observable
	fuelPerLap = 0;

	@observable
	fuelLeft = 0;

	@observable
	fuelCapacity = 0;

	@observable
	fuelUseActive = 0;

	@observable
	lastCheck = 0;

	@observable
	lapStartFuel = -1;

	@observable
	fuelLastLap = '';

	@observable
	fuelToEnd = '';

	@observable
	fuelToAdd = '';

	@observable
	playerSlotId = -1;

	@observable
	lastLapsFuel: number[] = [];

	constructor(props: IProps) {
		super(props);

		registerUpdate(this.update);
	}

	componentWillUnmount() {
		unregisterUpdate(this.update);
	}

	@action
	private update = () => {
		const nowCheck = Date.now();
		if (
			!lowPerformanceMode ||
			nowCheck - this.lastCheck >= 250
		) {
			this.fuelPerLap = r3e.data.FuelPerLap;
			this.fuelUseActive = r3e.data.FuelUseActive;
			this.fuelLeft = r3e.data.FuelLeft;
			this.fuelCapacity = r3e.data.FuelCapacity;
			this.lastCheck = nowCheck;
			this.fuelToEnd = getFuelNeeded(true);
			this.fuelToAdd = getFuelNeeded(false);
			if (this.playerSlotId === -1 && r3e.data.SteerInputRaw !== -1) {
				this.playerSlotId = r3e.data.VehicleInfo.SlotId;
			}
		}
		this.runFuelCheck();
	};

	private runFuelCheck() {
		if (this.playerSlotId !== -1) {
			const isPlayer = r3e.data.VehicleInfo.SlotId === this.playerSlotId;
			if (isPlayer) {
				if (
					r3e.data.LapDistance >= 0 &&
					this.lapStartFuel === -1 &&
					r3e.data.LapTimeCurrentSelf >= 0
				) {
					this.lapStartFuel = r3e.data.FuelLeft;
				}
				if (
					r3e.data.InPitlane > 0 ||
					r3e.data.GameInMenus > 0 ||
					r3e.data.FuelUseActive <= 0 ||
					r3e.data.SessionPhase <= 3
				) {
					this.lapStartFuel = -1;
				}
				if (
					this.lapStartFuel !== -1 &&
					r3e.data.LapDistance >= 0 &&
					r3e.data.LapDistance < 200 &&
					(this.lapStartFuel - r3e.data.FuelLeft) > (r3e.data.FuelPerLap * 0.5) &&
					(this.lapStartFuel - r3e.data.FuelLeft) < (r3e.data.FuelPerLap * 1.5)
				) {
					const nowFuel = r3e.data.FuelLeft;
					this.fuelLastLap = (this.lapStartFuel - nowFuel).toFixed(2);
					this.lapStartFuel = nowFuel;
					this.lastLapsFuel.push(parseFloat(this.fuelLastLap));
				}
			} else {
				this.lapStartFuel = -1;
			}
		} else {
			this.lapStartFuel = -1;
		}
	}

	private getFuelTextColor(fuel: number, laps: boolean) {
		const red = '#ff0000';
		const green = '#00ff00';

		let fromColor = green;
		let toColor = green;
		let amount = 0;

		// Magic numbers decided based on some random sample cars

		if (laps) {
			if (fuel < 2) {
				return red;
			}
			if (fuel > 4) {
				return green;
			}
			if (fuel > 2) {
				const localDelta = 2;
				const deltaFromCold = fuel - 2;
				amount = Math.min(1, deltaFromCold / localDelta);
				fromColor = red;
				toColor = green;
			}
		} else {
			if (fuel < (this.fuelCapacity * 0.05)) {
				return red;
			}
			if (fuel > (this.fuelCapacity * 0.25)) {
				return green;
			}
			if (fuel > (this.fuelCapacity * 0.05)) {
				const localDelta = this.fuelCapacity * 0.05;
				const deltaFromCold = fuel - localDelta;
				amount = Math.min(1, deltaFromCold / localDelta);
				fromColor = red;
				toColor = green;
			}
		}
		return lerpColor(fromColor, toColor, amount);
	}

	render() {
		if (!this.props.settings.subSettings.detailFuel.enabled) {
			return (
				<div
					{...widgetSettings(this.props)}
					className={classNames(style.fuel, this.props.className, {
						low: this.fuelLeft < this.fuelPerLap * 2
					})}
				>
					{(!!this.fuelUseActive || showAllMode) && (
						<div>
							<div className="fuelPerLap">
								{
									showAllMode
									?	12.34
									:	this.fuelPerLap.toFixed(1)
								}
							</div>
							<div className="barContainer">
								<div
									className="bar "
									style={{
										height: `${showAllMode
											?	50
											:	(this.fuelLeft /
											this.fuelCapacity) *
											100}%`
									}}
								/>
							</div>
							<SvgIcon src={require('./../../img/icons/fuel.svg')} />
							<div className="fuelLeft">
								{showAllMode
									?	109.3
									:	this.fuelLeft.toFixed(1)}
							</div>
						</div>
					)}
				</div>
			);
		}
		return (
			<div
				{...widgetSettings(this.props)}
				className={classNames(style.fuelDetail, this.props.className, {
				})}
			>
			{this.lapStartFuel}{' / '}{this.fuelLastLap}{' / '}{this.lastLapsFuel.length}
			<div className="FuelRemainBox"/>
				<div className="FuelReaminAmountBox">
					<div
						className="FuelRemain"
						style={{
							color: `${this.getFuelTextColor(this.fuelLeft, false)}`
						}}
					>
						{this.fuelLeft.toFixed(1)}
					</div>
				</div>
				<div className="FuelRemainTextBox">
					<div className="FuelRemainText">
						{_('Fuel Remain')}
					</div>
				</div>
				<div className="LapsEstimateBox"/>
				<div className="LapsEstimateAmountBox">
					<div
						className={classNames('LapsEstimateAmount', {
							lowLaps: (this.fuelLeft / this.fuelPerLap) < 1
						})}
						style={{
							color: `${this.getFuelTextColor(
								(this.fuelLeft / this.fuelPerLap), true
							)}`
						}}
					>
						{(this.fuelLeft / this.fuelPerLap).toFixed(1)}
					</div>
				</div>
				<div className="LapsEstimateTextBox">
					<div className="LapsEstimateText">
						{_('Laps Estimate')}
					</div>
				</div>
				<div className="FuelLastLapBox"/>
				<div className="FuelLastLapAmountBox">
					<div className="FuelLastLapAmount">
						{
							this.fuelLastLap !== ''
							?	this.fuelLastLap
							:	'N/A'
						}
					</div>
				</div>
				<div className="FuelLastLapTextBox">
					<div className="FuelLastLapText">
						{_('Last Lap')}
					</div>
				</div>
				<div className="FuelPerLapBox"/>
				<div className="FuelPerLapAmountBox">
					<div className="FuelPerLapAmount">
						{this.fuelPerLap.toFixed(2)}
					</div>
				</div>
				<div className="FuelPerLapTextBox">
					<div className="FuelPerLapText">
						{_('Fuel / Lap')}
					</div>
				</div>
				<div className="FuelToEndBox"/>
				<div className="FuelToEndAmountBox">
					<div className="FuelToEndAmount">
						{this.fuelToEnd}
					</div>
				</div>
				<div className="FuelToEndTextBox">
					<div className="FuelToEndText">
						{_('Fuel To End')}
					</div>
				</div>
				<div className="FuelToAddBox"/>
				<div className="FuelToAddAmountBox">
					<div className="FuelToAddAmount">
						{this.fuelToAdd}
					</div>
				</div>
				<div className="FuelToAddTextBox">
					<div className="FuelToAddText">
						{_('Fuel To Add')}
					</div>
				</div>
			</div>
		);
	}
}
